@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;700&family=Poppins:wght@200;300;400;600&display=swap);
:root {
  --color-highlight: #69bc47;

  --color-green-dark-1: #2e3832;
  --color-green-dark-2: #32601c;
  --color-green-light-1: #f8fff9;

  --color-grey-dark-1: #222925;
