  --color-grey-dark-3: #777777;

  --color-grey-light-1: #f4f4f4;
  --color-grey-light-2: #d8d8d8;
  --color-grey-light-3: #f8f8f8;

  --color-white: #fff;
  --color-black: #000;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

/* Tab-land 1200px*/
@media only screen and (max-width: 75em) {
  html {
    font-size: 58%;
  }
}

/* Tab-port  900px*/
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 54%;
  }
}

/* Phone  600px*/
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 32%;
  }
}

body {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  line-height: 1.6;
  color: #4b4d4b;
  color: var(--color-grey-dark-2);
  background-color: #fff;
  background-color: var(--color-white);
}

h1,
h2,
h3,
h4 {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
  font-size: 2rem;
}

p {
