}

.btn-main:link,
.btn-main:visited,
.btn-main,
.btn-secondary:link,
.btn-secondary:visited,
.btn-secondary,
.btn-add:link,
.btn-add:visited,
.btn-add,
.btn-outline:link,
.btn-outline:visited,
.btn-outline {
  all: unset;
  cursor: pointer;
  color: #fff;
  color: var(--color-white);
  font-size: 1.5rem;
  padding: 0.5rem 2rem;
  border-radius: 20rem;
  transition: all 0.2s ease-in;
}

/* Main */
.btn-main {
  background: linear-gradient(
    180deg,
    rgba(105, 188, 71, 1) 0%,
    rgba(83, 146, 57, 1) 100%
  );
}

.btn-main:hover {
  -webkit-transform: translateY(-0.3rem);
          transform: translateY(-0.3rem);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
}

.btn-main:active,
.btn-main:focus {
  -webkit-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

/* Secondary */
.btn-secondary {
  background: linear-gradient(
    180deg,
    rgba(86, 88, 87, 1) 0%,
    rgba(0, 0, 0, 1) 100%
